<template>
  <!--  -->
  <div >
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item>Banner</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()">Banner</el-button>
      </el-form-item>
    </el-form>
    <div v-loading="loading" border element-loading-text="" style="margin-top:30px">
      <el-table size="small" :data="imglist" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 80%;">
        <el-table-column prop="id" label="ID" width="50"></el-table-column>
        <el-table-column prop="img" label="Banner" width="200">
          <div  slot-scope="scope">
            <img class="table_img" :src="scope.row.img" alt=""/>
          </div>
        </el-table-column>
        <el-table-column prop="url" label=""></el-table-column>
        <el-table-column prop="statusname" label="" width="80"></el-table-column>
        <el-table-column sortable prop="sort" label="" width="70">
        </el-table-column>
        <el-table-column align="center" label="" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)" v-if="scope.row.status==1"></el-button>
              <el-button size="mini" type="primary" @click="delRecord(scope.$index, scope.row)" v-else></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="60%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="ID" prop="id" >
            <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="editForm.img" auto-complete="off" placeholder="icon"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-preview="handlePictureCardPreview"
              :before-upload="beforeupload"
              :on-success="handleAvatarSuccess"
              :file-list="fileList"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary"></el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="type">
            <el-select size="small" v-model="editForm.type" placeholder="" class="userRole">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input size="small" v-model="editForm.url" auto-complete="off" placeholder=""></el-input>
            <div>&nbsp;ID&nbsp;ID H5&nbsp;,appcommunityrelease</div>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select size="small" v-model="editForm.status" placeholder="" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()"></el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog :title="title" :visible.sync="addFormVisible" width="60%" @click="closeDialog">
        <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="addForm.img" auto-complete="off" placeholder="icon"></el-input>
             <el-upload
              class="upload-demo"
              ref="upload"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-preview="handlePictureCardPreview"
              :before-upload="beforeupload"
              :on-success="handleAvatarSuccess"
              :file-list="fileList"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary"></el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="type">
            <el-select size="small" v-model="addForm.type" placeholder="" class="userRole">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input size="small" v-model="addForm.url" auto-complete="off" placeholder=""></el-input>
            <div>&nbsp;ID&nbsp;ID H5&nbsp;,appcommunityrelease</div>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select size="small" v-model="addForm.status" placeholder="" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()"></el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getNewsImgList,
  imgDo
} from '../../api/apiConfig'
  export default {
    data() {
      return {
        dialogImageUrl: '',
        dialogVisible: false,
        loading: false, //
        title: 'Banner',
        imglist: [],
        editForm: {
          act:"update",
          id: '',
          category:"2",
          img: '',
          status: '',
          type: 4,
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        addForm: {
          act:"add",
          category:"2",
          img: '',
          status: '',
          type: 4,
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        listStatus:[{id:1,name:""},{id:2,name:""}],
        listType:[{id:4,name:""},{id:1,name:""},{id:2,name:""},{id:3,name:"H5"},{id:5,name:"APP"}],
        editFormVisible: false,
        addFormVisible: false,
        rules: {
          sort: [
            { required: true, message: '', trigger: 'blur' }
          ],
          status: [{ required: true, message: '', trigger: 'blur' }]
        },
        fileList: [],
        param1: {
          category:2
        },
      };
    },
    /**
     * 
     */
    created() {
      this.getdata(this.param1)
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 
      getdata(param1) {
        this.loading = true;
        let status_list = {1:'',2:''}
        getNewsImgList(param1)
        .then(res => {
          this.loading = false;
          if (res.errcode === 0) {
            let imglist = res.data.imglist;
            for (var i = imglist.length - 1; i >= 0; i--) {
              let statusid = imglist[i]['status']
              imglist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.imglist  = imglist
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
      },
      //
      delRecord(index, row) {
        let param = {
          act:"upstatus",
          status:2,
          id:row.id
        };
        param.status = row.status==1 ? 2 : 1
        let actname = row.status==1 ? '' : ''
        this.$confirm(''+actname+'?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            imgDo(param)
              .then(res => {
                if (res.errcode === 0) {
                  this.$message({
                    type: 'success',
                    message: '!'
                  })
                  this.getdata(this.param1)
                } else {
                  this.$message({
                    type: 'info',
                    message: res.errmsg
                  })
                }
              })
              .catch(err => {
                this.loading = false
                this.$message.error('')
              })
          })
          .catch((err) => {
            console.log('err:',err)
            // this.$message({
            //   type: 'info',
            //   message: ''
            // })
          })
      },
      //
      handleAdd: function(index, row) {
        this.addFormVisible = true
        this.title = 'Banner'
        this.type = 2
        this.addForm.img = ''
        this.addForm.url = ''
        this.addForm.sort = 1
        this.addForm.status = 1
        this.addForm.type = 4
      },
      submitAddForm() {
        imgDo(this.addForm)
          .then(res => {
            this.addFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: ''
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.addFormVisible = false
            this.loading = false
            this.$message.error('')
          })
      },
      //
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.title = 'Banner'
        this.type = 1
        this.editForm.id = row.id
        this.editForm.sort = row.sort
        this.editForm.img = row.img
        this.editForm.type = row.type
        this.editForm.url = row.url
        this.editForm.status = row.status
      },
      // 
      submitEditForm() {
        // console.log("editData:",this.editForm)
        imgDo(this.editForm)
          .then(res => {
            this.editFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: ''
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.editFormVisible = false
            this.loading = false
            this.$message.error('')
          })
      },
      // 
      closeDialog() {
        this.editFormVisible = false
        this.addFormVisible = false
      },
      //upload
      beforeupload(file) {
          console.log("file:",file)
          // return false;
      },
      beforeremove(file,fileList){
        let that = this;
        that.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
          }).then(() => {
              that.asyncReq(file,fileList) // 
          }).catch(() => {
              that.$message({
                  type: 'info',
                  message: ''
              });
        });
        return false; // ,false
      },

      onchange(file,fileList){
        this.param.img = file.url;
        this.param.title = file.name;
        // console.log("onchang-param:",this.param)
        // console.log("fileList:",fileList);
        // console.log("file:",file)
      },
      submitUpload() {
        this.$refs.upload.submit();
      },
      // 
      handleAvatarSuccess(response,file,fileList){
        this.addForm.img = response.data;
        this.editForm.img = response.data;
      }
    }
  }
</script>
<style>
  .user-search {
    margin-top: 20px;
  }
  .table_img{
    max-width: 100%;
    height: auto;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
</style>



