// 
import Vue from 'vue';
import Router from 'vue-router';
// 
import login from '@/views/login';
// 
import index from '@/views/index';
/**
 * 
 */
import guide from '@/views/guides/guide';// 
import banner from '@/views/banner/banner';// banner
import banner_china_girl from '@/views/banner/china_girl';// banner
import first_news from '@/views/News/first_news';//
import hotsearch from '@/views/common/hotsearch';//

/**
 *
 */
// 
import News from '@/views/News/News';//
import editNews from '@/views/News/editNews';//
import news_type from '@/views/News/news_type';//
import news_comment from '@/views/News/news_comment';//


/**
 * 
 */

// 
import talks from '@/views/talks/talks';
import talk_detail from '@/views/talks/talk_detail';
import talklabel from '@/views/talks/talklabel';

/**
 * 
 */

// 
import admin from '@/views/admin/admin';
import user from '@/views/admin/user';

//
import message_push from '@/views/message/message_push';
import member_device from '@/views/message/member_device';

// 
import feedback from '@/views/other/feedback';
import eport from '@/views/other/eport';


// 
Vue.use(Router);

//  
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, 
    {
        path: '/login',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, 
    {
        path: '/index',
        name: '',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [
        {
            path: '/guides/guide',
            name: '',
            component: guide,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/banner/banner',
            name: 'banner',
            component: banner,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/banner/china_girl',
            name: 'banner',
            component: banner_china_girl,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/News',
            name: '',
            component: News,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/admin/admin',
            name: '',
            component: admin,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/editNews',
            name: '',
            component: editNews,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/talks/talks',
            name: '',
            component: talks,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/talks/talk_detail',
            name: '',
            component: talk_detail,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/news_type',
            name: '',
            component: news_type,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/news_comment',
            name: '',
            component: news_comment,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/first_news',
            name: '',
            component: first_news,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/common/hotsearch',
            name: '',
            component: hotsearch,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/admin/user',
            name: '',
            component: user,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/talks/talklabel',
            name: '',
            component: talklabel,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/other/feedback',
            name: '',
            component: feedback,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/other/eport',
            name: '',
            component: eport,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/message/message_push',
            name: '',
            component: message_push,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/message/member_device',
            name: '',
            component: member_device,
            meta: {
                requireAuth: true
            }
        }

        ]
    }]
})


