/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/News/News' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div style="padding: 30px;">
      <!---->
      <el-table size="small" :data="listData" highlight-current-row v-loading="loading" element-loading-text="" style="width: 100%;">
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column sortable prop="news_id" label="ID" width="100"></el-table-column>
        <el-table-column sortable prop="member_name" label="" width="200">
        </el-table-column>
        <el-table-column sortable prop="menber_heading" label="" width="150">
          <img class="table_img" src="../../assets/img/logo.png" alt=""/>
        </el-table-column>
        <el-table-column sortable prop="comment" label="" width="180">
          <template slot-scope="scope">
            <div>{{scope.row.time_create|timestampToTime}}</div>
          </template>
        </el-table-column>
        <el-table-column sortable prop="comment" label="" min-width="200"></el-table-column>
        <el-table-column align="center" label="" width="100">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="deleteCommont(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
          <!-- <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination> -->
      </div>
    </div>
  </div>
</template>

<script>
import {
  NewscommentList,
  NewscommentDel
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      param: {
        news_id:0
      },
      detailData: {
        title:'',
        views:'',
        loves:'',
        comment:'',
        author_name:'',
        author_headimg:''
      }, //
      head_img:'',
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
  },
  mounted () {
    this.pageInit();
  },
  /**
   * 
   */
  methods: {
    pageInit(){
      let list = this.$route.query;
      this.param.news_id = list.id;
      this.getdata(this.param)
    },
    //
    getdata(param) {
      this.loading = true
      NewscommentList(param)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.listData = res.data.commentlist;
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      // this.formInline.limit = parm.pageSize
      this.getataList(this.formInline)
    },
    // 
    deleteCommont(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(() => {
        NewscommentDel({id:row.id})
          .then(res => {
            if (res.errcode) {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getdata(this.param)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            console.log('err:',err)
            this.loading = false
            this.$message.error('')
          })
      })
      .catch((err) => {
        console.log('err:',err)
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
.el-form-item__label{
  text-align:left;
}
.dialog-footer{
  text-align:center;
}
.detail{
  margin:50px
}
.detail-item{
  padding:20px;min-width:100px;
}
.label-h1{
  float:left;display:inline;font-size: 20px;font-weight: bold;
}
.label-text{
  float:left;display:inline;font-size: 20px;
}
</style>

 



