/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/talks/talks' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin: 50 200 50 50;">
        <div class="detail">
          <el-form label-width="120px" ref="editForm">
            <el-form-item label="">
              <label class="label-text">{{detailData.title}}</label>
            </el-form-item>
            <el-form-item label="">
              <label class="label-text" v-if="detailData.img">
                  <div class="imgli" v-for="item in detailData.img" :key="item">
                    <img :src="item" alt=""/>
                  </div>
              </label>
            </el-form-item>
            <el-form-item label="">
              <div slot-scope="scope" class="video">
                <video :src="detailData.audio_video" controls="controls" v-if="detailData.audio_video"> video </video>
              </div>
            </el-form-item>
            <el-form-item label=": ">
              <label class="label-text">{{detailData.views}}</label>
            </el-form-item>

            <el-form-item label=": ">
              <label class="label-text">{{detailData.comment}}</label>
            </el-form-item>
            <el-form-item label=": ">
              <label class="label-text">{{detailData.loves}}</label>
            </el-form-item>
            <el-form-item label=": ">
              <label class="label-text">{{detailData.labels}}</label>
            </el-form-item>
            <el-form-item label=": ">
              <label class="label-text">{{detailData.author_name}}</label>
            </el-form-item>
            <el-form-item label=": ">
              <img :src="detailData.author_headimg" width="80">
            </el-form-item>
            <el-form-item label=": ">
                <!---->
                <el-table size="small" :data="listData" highlight-current-row v-loading="loading" element-loading-text="" style="width: 100%;">
                  <el-table-column prop="id" label="ID" width="100"></el-table-column>
                  <el-table-column prop="member_name" label="" width="120"></el-table-column>
                  <el-table-column prop="member_heading" label="" width="90">
                    <div slot-scope="scope">
                      <img class="table_img" :src="scope.row.member_headimg" alt=""/>
                    </div>
                  </el-table-column>
                  <el-table-column prop="comment" label="" min-width="200"></el-table-column>
                  <el-table-column prop="comment" label="" width="150">
                    <template slot-scope="scope">
                      <div>{{scope.row.time_create|timestampToTime}}</div>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" label="" width="100">
                    <template slot-scope="scope">
                      <el-button size="mini" type="danger" @click="deleteCommont(scope.$index, scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  commontConfigList,
  detailConfigList,
  CommunitycommentDel
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      detailParam: {id:0},
      detailData: {}, //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.detailParam)
  },
  mounted () {
    this.pageInit();
  },
  /**
   * 
   */
  methods: {
    pageInit(){
      let list = this.$route.query;
      this.detailParam.id = list.id;
    },
    // 
    getdata(parameter) {
      this.loading = true
      /***
       * 
       */
        detailConfigList(parameter)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.detailData = res.data.community;
            this.listData = res.data.commentlist;
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    deleteCommont(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          CommunitycommentDel({id:row.id})
            .then(res => {
              if (res.errcode==0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.detailParam)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: ''
          // })
        })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.imgli{
  display: inline-block; 
  margin-right: 10px;
}
.video video{
  max-height: 200px;
}
.imgli img{
  height: 80px;
  max-width:200px;
}
.table_img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.el-form-item__label{
  text-align:left;
}
.dialog-footer{
  text-align:center;
}
.detail{
  margin:50px
}
.detail-item{
  padding:20px;min-width:100px;
}
</style>

 



