/**
 *  
 */
 <template>
  <!--  -->
  <div >
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()"></el-button>
      </el-form-item>
    </el-form>
    <div v-loading="loading" border element-loading-text="" style="margin-top:30px">
      <el-table size="small" :data="datalist" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 80%;">
        <el-table-column prop="id" label="ID" width="50"></el-table-column>
        <el-table-column prop="typename" label="" width="80"></el-table-column>
        <el-table-column prop="content" label=""></el-table-column>
        <el-table-column prop="statusname" label="" width="80"></el-table-column>
        <el-table-column sortable prop="hot" label="" width="80"></el-table-column>
        <el-table-column sortable prop="sort" label="" width="80"></el-table-column>
        <el-table-column align="center" label="" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)" v-if="scope.row.status==1"></el-button>
            <el-button size="mini" type="primary" @click="delRecord(scope.$index, scope.row)" v-else></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="60%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="ID" prop="id" >
            <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="type">
            <el-select size="small" v-model="editForm.type" placeholder="" class="userRole">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-input size="small" v-model="editForm.content" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-input size="small" v-model="editForm.hot" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select size="small" v-model="editForm.status" placeholder="" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()"></el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog :title="title" :visible.sync="addFormVisible" width="60%" @click="closeDialog">
        <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
          <el-form-item label="" prop="type">
            <el-select size="small" v-model="addForm.type" placeholder="" class="userRole">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-input size="small" v-model="addForm.content" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-input size="small" v-model="addForm.hot" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select size="small" v-model="addForm.status" placeholder="" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()"></el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getHotsearchList,
  hotsearchDo
} from '../../api/apiConfig'
  export default {
    data() {
      return {
        dialogImageUrl: '',
        dialogVisible: false,
        loading: false, //
        title: '',
        editForm: {
          act:"update",
          id: '',
          type: 1,
          status: '',
          content: '',
          hot: 0,
          sort: 255,
          token: localStorage.getItem('logintoken')
        },
        addForm: {
          act:"add",
          type: 1,
          status: 1,
          content: '',
          hot: 0,
          sort: 255,
          token: localStorage.getItem('logintoken')
        },
        listStatus:[{id:1,name:""},{id:2,name:""}],
        listType:[{id:1,name:""},{id:2,name:""}],
        editFormVisible: false,
        addFormVisible: false,
        rules: {
          sort: [
            { required: true, message: '', trigger: 'blur' }
          ],
          status: [{ required: true, message: '', trigger: 'blur' }]
        },
        datalist: [],
        param1: {
          category:2
        },
      };
    },
    /**
     * 
     */
    created() {
      this.getdata(this.param1)
    },
    methods: {
      // 
      getdata(param1) {
        this.loading = true;
        let status_list = {1:'',2:''}
        let type_list = {1:'',2:''}
        getHotsearchList(param1)
        .then(res => {
          this.loading = false;
          if (res.errcode === 0) {
            let datalist = res.data.datalist;
            for (var i = datalist.length - 1; i >= 0; i--) {
              let statusid = datalist[i]['status']
              datalist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
               let typeid = datalist[i]['type']
              datalist[i]['statusname'] = type_list[typeid] ? type_list[typeid] : typeid
            }
            this.datalist  = datalist
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
          console.log('err:',err)
        })
      },
      //
      delRecord(index, row) {
        let param = {
          act:"upstatus",
          status:2,
          id:row.id
        };
        param.status = row.status==1 ? 2 : 1
        let actname = row.status==1 ? '' : ''
        this.$confirm(''+actname+'?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            hotsearchDo(param)
              .then(res => {
                if (res.errcode === 0) {
                  this.$message({
                    type: 'success',
                    message: '!'
                  })
                  this.getdata(this.param1)
                } else {
                  this.$message({
                    type: 'info',
                    message: res.errmsg
                  })
                }
              })
              .catch(err => {
                this.loading = false
                this.$message.error('')
              })
          })
          .catch((err) => {
            console.log('err:',err)
            // this.$message({
            //   type: 'info',
            //   message: ''
            // })
          })
      },
      //
      handleAdd: function(index, row) {
        this.addFormVisible = true
        this.title = ''
      },
      submitAddForm() {
        hotsearchDo(this.addForm)
          .then(res => {
            this.addFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: ''
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.addFormVisible = false
            this.loading = false
            this.$message.error('')
          })
      },
      //
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.title = ''
        this.editForm.id = row.id
        this.editForm.type = row.type
        this.editForm.content = row.content
        this.editForm.status = row.status
        this.editForm.hot = row.hot
        this.editForm.sort = row.sort
      },
      // 
      submitEditForm() {
        // console.log("editData:",this.editForm)
        hotsearchDo(this.editForm)
          .then(res => {
            this.editFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: ''
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.editFormVisible = false
            this.loading = false
            this.$message.error('')
          })
      },
      // 
      closeDialog() {
        this.editFormVisible = false
        this.addFormVisible = false
      }
    }
  }
</script>
<style>
  .user-search {
    margin-top: 20px;
  }
  .table_img{
    max-width: 100%;
    height: auto;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
</style>



