/**
 * 
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <!-- <el-form :inline="true" :model="searchParam" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="searchParam.id" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form> -->
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 80%;">
      <el-table-column sortable prop="id" label="ID" width="100">
      </el-table-column>
      <el-table-column sortable prop="name" label="" width="200">
      </el-table-column>
      <el-table-column sortable prop="status" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="sort" label="" width="100">
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="ID" prop="id" >
          <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select size="small" v-model="editForm.status" placeholder="" class="userRole">
            <el-option
            v-for="item in listStatus"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog :title="title" :visible.sync="addFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="addForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select size="small" v-model="addForm.status" placeholder="" class="userRole">
            <el-option
            v-for="item in listStatus"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  CommunitylabelList,
  CommunitylabelDo
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      addFormVisible: false,
      title: '',
      type:2, //1 2
      editForm: {
        act:"update",
        id: '',
        name: '',
        status: '',
        sort: '',
        token: localStorage.getItem('logintoken')
      },
      addForm: {
        act:"add",
        name: '',
        status: 1,
        sort: '',
        token: localStorage.getItem('logintoken')
      },
      searchParam:{
        act:"get",
        id:""
      },
      // rules
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        status: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {},
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      listStatus:[{id:1,name:""},{id:2,name:""}],
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       * 
       */
      parameter.pagesize = this.pageparm.pageSize
      CommunitylabelList(parameter)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.listData  = res.data.labellist;
            // 
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      // console.log("searchParam",this.searchParam)
      this.getdata(this.searchParam)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      this.title = ''
      this.type = 1
      this.editForm.id = row.id
      this.editForm.name = row.name
      this.editForm.sort = row.sort
      this.editForm.status = row.status
    },
    // 
    submitEditForm() {
      // console.log("editData:",this.editForm)
      CommunitylabelDo(this.editForm)
        .then(res => {
          this.editFormVisible = false
          this.loading = false
          if (res.errcode === 0) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: ''
            })
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.editFormVisible = false
          this.loading = false
          this.$message.error('')
        })
    },
     //
    handleAdd: function(index, row) {
      this.addFormVisible = true
      this.title = ''
      this.type = 2
      this.addForm.id = ''
      this.addForm.name = ''
      this.addForm.sort = ''
      this.addForm.status = 1
    },
    // 
    submitAddForm() {
      CommunitylabelDo(this.addForm)
        .then(res => {
          this.addFormVisible = false
          this.loading = false
          if (res.errcode === 0) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: ''
            })
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.addFormVisible = false
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    deleteUser(index, row) {
      let param = {
        act:"delete",
        id:row.id
      };
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          CommunitylabelDo(param)
            .then(res => {
              if (res.errcode === 0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



