/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" class="user-search"></el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
      <el-table-column  prop="type" label="" width="100"></el-table-column>
      <el-table-column sortable prop="time_create" label="">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" min-width="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleAction(scope.row)"></el-button>
          <el-button size="mini" type="primary" @click="cancelAction(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  eportDo
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
import timestampToTime from '../../utils/util'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      formInline: {
        keywords:"",
        token: localStorage.getItem('logintoken'),
      },
      listData: [],
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true;
      eportDo(parameter)
        .then(res => {
          this.loading = false
          //
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            this.listData = res.data.datalist;
            // 
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          }  else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          console.log('err:',err)
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    // 
    handleAction(row) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          eportDo({id: row.id,act:'update',status:2})
            .then(res => {
              if (res.errcode==0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    cancelAction(row) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          eportDo({id: row.id,act:'update',status:1})
            .then(res => {
              if (res.errcode==0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



