/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="searchParam" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="searchParam.keywords" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <!-- <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button> -->
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="id" label="ID" width="70"></el-table-column>
      <el-table-column prop="title" label="" min-width="300">
      </el-table-column>
      <el-table-column prop="audio_video_img" label=" || " width="200">
        <template slot-scope="scope">
          <video :src="scope.row.audio_video" controls="controls" v-if="scope.row.audio_video"> video </video>
          <img class="table_img" :src="scope.row.img" alt="" v-if="scope.row.img" />
        </template>
      </el-table-column>
      <el-table-column prop="author_id" label="" width="100"></el-table-column>
      <el-table-column sortable prop="loves" label="" width="100"></el-table-column>
      <el-table-column sortable prop="views" label="" width="100"></el-table-column>
      <el-table-column sortable prop="comment" label="" width="100"></el-table-column>
      <el-table-column sortable prop="time_create" label="" width="140">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="toComment(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  talkConfigList,
  communityDo
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      id:0,
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      searchParam:{
        act:"get",
        keywords:""
      },
      formInline: {
        "page":"1",
        // "cate_id":"1"//123
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 5,
        total: 1
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       * 
       */
      parameter.pagesize = this.pageparm.pageSize
      talkConfigList(parameter)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.listData = res.data.contentlist;
            // console.log("listData:",this.listData)
            // 
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.searchParam)
    },
    // 
    delRecord(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(() => {
        communityDo({id:row.id,status:'-1'}).then(res => {
            if (res.errcode===0) {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getdata(this.formInline)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.loading = false
            this.$message.error('')
          })
      })
      .catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: ''
        // })
      })
    },
    //
    toComment(index,row){
      // console.log("index:",index)
      // console.log("row:",row)
      this.$router.push({ path: '/talks/talk_detail?id='+row.id})
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
video{
  width: 100%;
  height: auto;
}
.table_img{
  width: 100%;
  height: auto;
}
</style>

 



