/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAddd()"></el-button>
      </el-form-item>
    </el-form> -->
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="formInline.keywords" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="id" label="ID" width="80">
      </el-table-column>
      <el-table-column prop="name" label="" width="100">
      </el-table-column>
      <el-table-column  prop="phone" label="">
      </el-table-column>
      <el-table-column prop="headimg" label="" width="80">
        <div  slot-scope="scope">
          <img class="table_img" :src="scope.row.headimg" alt=""/>
        </div>
      </el-table-column>
      <el-table-column sortable prop="sex" label="" width="80">
      </el-table-column>
      <el-table-column prop="country" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="province" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="city" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="age" label="" width="80">
      </el-table-column>
      <!-- <el-table-column sortable prop="weibo" label="" width="100"></el-table-column> -->
      <!-- <el-table-column sortable prop="role" label="" width="80"></el-table-column> -->
      <el-table-column sortable prop="time_create" label="">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" min-width="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog :title="title" :visible.sync="addFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="addForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select size="small" v-model="addForm.status" placeholder="" class="userRole">
            <el-option
            v-for="item in listUser"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  adminConfigList,
  adminDel
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
import timestampToTime from '../../utils/util'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      addFormVisible: false, //
      title: '',
      addForm: {
        act:"add",
        name: '',
        status: '',
        sort: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        keywords:"",
        token: localStorage.getItem('logintoken'),
      },
      listData: [],
      listUser: [],
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        status: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true;
      adminConfigList(parameter)
        .then(res => {
          this.loading = false
          //
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            this.listData = res.data.memberlist;
            // 
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          }  else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          console.log('err:',err)
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleAddd: function(index, row) {
      this.addFormVisible = true
      this.title = ''
    },
    submitAddForm() {
      cateDo(this.addForm)
        .then(res => {
          this.addFormVisible = false
          this.loading = false
          if (res.errcode === 0) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: ''
            })
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.addFormVisible = false
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    deleteUser(index, row) {
        this.$confirm(''+ row.name +'?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          adminDel({id: row.id})
            .then(res => {
              if (res.errcode==0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },

    // 
    closeDialog() {
      this.addFormVisible = false
    },
    //  
    dateFormat(row, column) {  
      var date = row[column.property];  
      if (date == undefined) {  
         return "";  
      }  
      return moment(date).format("YYYY-MM-DD HH:mm:ss");  
    }  
        
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



