<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()"></el-button>
      </el-form-item>
    </el-form>
    <div v-loading="loading" border element-loading-text="" style="margin: 20px;">
      <el-table size="small" :data="imglist" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
        <el-table-column prop="id" label="ID" width="50"></el-table-column>
        <el-table-column prop="title" label="" width="160"></el-table-column>
        <el-table-column prop="img" label="" width="120">
          <div  slot-scope="scope">
            <img class="table_img" :src="scope.row.img" alt=""/>
          </div>
        </el-table-column>
        <el-table-column prop="url" label=""></el-table-column>
        <el-table-column prop="statusname" label="" width="80"></el-table-column>
        <el-table-column sortable prop="sort" label="" width="70">
        </el-table-column>
        <el-table-column align="center" label="" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)" v-if="scope.row.status==1"></el-button>
              <el-button size="mini" type="primary" @click="delRecord(scope.$index, scope.row)" v-else></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="40%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="ID" prop="id" >
            <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input size="small" v-model="editForm.title" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="editForm.img" auto-complete="off" placeholder="icon"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload2"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-success="handleAvatarSuccess2"
              :file-list="fileList2"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary"></el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload2"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input size="small" v-model="editForm.url" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select size="small" v-model="editForm.status" placeholder="" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()"></el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog :title="title" :visible.sync="addFormVisible" width="40%" @click="closeDialog">
        <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">
          <el-form-item label="" prop="title">
            <el-input size="small" v-model="addForm.title" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="addForm.img" auto-complete="off" placeholder="icon"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-success="handleAvatarSuccess"
              :file-list="fileList"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary"></el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input size="small" v-model="addForm.url" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select size="small" v-model="addForm.status" placeholder="" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog"></el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()"></el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getNewsImgList,
  imgDo
} from '../../api/apiConfig'
  export default {
    data() {
      return {
        form: {//form
            name: ''
         },
        param:  {
          act:"add",
          category:"3",
          title:"",
          img:"./static/logo.png",
          url:"#",
          sort:"1",
        },//
        title: '',
        editForm: {
          act:"update",
          id: '',
          category:"3",
          title: '',
          img: '',
          status: '',
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        addForm: {
          act:"add",
          category:"3",
          title: '',
          img: '',
          status: '',
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        listStatus:[{id:1,name:""},{id:2,name:""}],
        dialogImageUrl: '',
        editFormVisible: false,
        addFormVisible: false,
        loading: false, //
        imglist: [],
        rules: {
          title: [
            { required: true, message: '', trigger: 'blur' }
          ],
          sort: [
            { required: true, message: '', trigger: 'blur' }
          ],
          status: [{ required: true, message: '', trigger: 'blur' }]
        },
        param1: {
          category:3
        },
        fileList: [],
        fileList2: [],
      };
    },
    /**
     * 
     */
    created() {
      this.getdata(this.param1)
    },
    methods: {
      // 
      getdata(param1) {
        this.loading = true;
        let status_list = {1:'',2:''}
        getNewsImgList(param1)
        .then(res => {
          this.loading = false;
          if (res.errcode === 0) {
            let imglist = res.data.imglist;
            for (var i = imglist.length - 1; i >= 0; i--) {
              let statusid = imglist[i]['status']
              imglist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.imglist  = imglist
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
      },
      // 
      delRecord(index, row) {
        let param = {
          act:"upstatus",
          status:2,
          id:row.id
        };
         param.status = row.status==1 ? 2 : 1
        let actname = row.status==1 ? '' : ''
        this.$confirm(''+actname+'?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            imgDo(param)
              .then(res => {
                if (res.errcode === 0) {
                  this.$message({
                    type: 'success',
                    message: '!'
                  })
                  this.getdata(this.param1)
                } else {
                  this.$message({
                    type: 'info',
                    message: res.errmsg
                  })
                }
              })
              .catch(err => {
                this.loading = false
                this.$message.error('')
              })
          })
          .catch((err) => {
            console.log('err:',err)
            // this.$message({
            //   type: 'info',
            //   message: ''
            // })
          })
      },
       //
      handleAdd: function(index, row) {
        this.addFormVisible = true
        this.title = ''
        this.type = 2
        this.addForm.title = ''
        this.addForm.img = ''
        this.addForm.url = ''
        this.addForm.sort = 1
        this.addForm.status = 1
      },
      submitAddForm() {
        imgDo(this.addForm)
          .then(res => {
            this.addFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: ''
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.addFormVisible = false
            this.loading = false
            this.$message.error('')
          })
      },
      //
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.title = ''
        this.type = 1
        this.editForm.id = row.id
        this.editForm.title = row.title
        this.editForm.sort = row.sort
        this.editForm.img = row.img
        this.editForm.url = row.url
        this.editForm.status = row.status
      },
      // 
      submitEditForm() {
        // console.log("editData:",this.editForm)
        imgDo(this.editForm)
          .then(res => {
            this.editFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: ''
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.editFormVisible = false
            this.loading = false
            this.$message.error('')
          })
      },
      // 
      closeDialog() {
        this.editFormVisible = false
        this.addFormVisible = false
      },
      submitUpload() {
        this.$refs.upload.submit();
      },
      // 
      handleAvatarSuccess(response,file,fileList){
        this.addForm.img = response.data;
      },
      submitUpload2() {
        this.$refs.upload2.submit();
      },
      // 
      handleAvatarSuccess2(response,file,fileList){
        this.editForm.img = response.data;
      }
    }
  }
</script>
<style>
 img{
  max-width: 100%;
  height: auto;
 }
  .user-search {
    margin-top: 20px;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
</style>



