/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()"></el-button>
    </el-form>
    <!--  -->
    <el-form :inline="true" :model="searchParam" class="user-search">
      <el-form-item>
        <el-select v-model="searchParam.cate_id" placeholder="">
          <el-option
            v-for="item in listCate"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="searchParam.keywords" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="id" label="ID" width="50"></el-table-column>
      <el-table-column prop="type" label="" width="66"></el-table-column>
      <el-table-column sortable prop="cate_name" label="" width="80"></el-table-column>
      <el-table-column sortable prop="title" label="" min-width="200"></el-table-column>
      <el-table-column prop="imgs" label="" width="120">
        <div  slot-scope="scope">
        <img class="table_img" :src="scope.row.imgs" alt=""/>
        </div>
      </el-table-column>
      <!-- <el-table-column sortable prop="content" label="" width="120" :formatter="formatText"></el-table-column> -->
      <el-table-column prop="author_name" label="" width="80"></el-table-column>
      <!-- <el-table-column prop="payOpen" label="" width="70">
        <div  slot-scope="scope">
          <img class="table_img" :src="scope.row.author_headimg" alt=""/>
        </div>
      </el-table-column> -->
      <el-table-column sortable prop="time_create" label="" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="statusname" label="" width="90"></el-table-column>
      <el-table-column sortable prop="loves" label="" width="100"></el-table-column>
      <el-table-column sortable prop="views" label="" width="100"></el-table-column>
      <el-table-column sortable prop="comment" label="" width="100"></el-table-column>
      <el-table-column align="center" label="" width="380">
        <template slot-scope="scope">
          <el-button size="mini" @click="toComment(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row.id)"></el-button>
          <el-button size="mini" type="primary" @click="upStatus(scope.$index, scope.row)">{{scope.row.status==1?'':''}}</el-button>
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row,2)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  getNewsList,
  getNewsListSave,
  NewsListDelete
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      formInline: {
        // page: 1,
        // limit: 10,
        page:1,
        // cate_id:1,
        token: localStorage.getItem('logintoken')
      },
      searchParam:{
        act:"get",
        keywords:"",
        cate_id: "all"
      },
      userparm: [], //
      listData: [], //
      listCate: [],//
      cateId: 'all',//Id
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 20,
        total: 1
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      let type_list = {1:"",2:"",3:""}
      let status_list = {1:"",2:""}
      this.loading = true
      parameter['pagesize'] = parameter['pagesize'] ? parameter['pagesize'] : this.pageparm['pageSize']
      getNewsList(parameter)
        .then(res => {
          this.loading = false
          //
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            let contentlist = res.data.contentlist
            for (var i = contentlist.length - 1; i >= 0; i--) {
              let typeid = contentlist[i]['type']
              contentlist[i]['type'] = type_list[typeid] ? type_list[typeid] : typeid

              let statusid = contentlist[i]['status']
              contentlist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.listData = contentlist
            this.listCate = res.data.cate_list
            this.searchParam.cate_id = res.data.cate_id
            // 
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.searchParam)
    },
    //
    handleEdit: function(index, row,type) {
      this.$router.push({ path: '/News/editNews?id='+row.id})
    },
    handleAdd(type){
      this.$router.push({ path: '/News/editNews'})
    },
    //
    toComment: function(index, row) {
      // let data = JSON.str this.listData;
      // console.log("row:",row)
      let param = JSON.stringify(row);
      // console.log("this.listData:",this.listData)
      // console.log("param:",param)
      this.$router.push({ path: '/News/news_comment?id='+row.id })
    },
    // 
    deleteUser(index, id) {
      // console.log("id",id)
      let that = this;
      let data = {
        "act":"delete",
        "id":id
      }
      that.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(() => {
        // console.log("data",data)
        NewsListDelete(data)
          .then(res => {
            if (res.errcode == 0) {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.getdata(this.formInline)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.loading = false
            this.$message.error('')
          })
      })
      .catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: ''
        // })
      })
    },
    // 
    upStatus(index, row) {
      // console.log("row",row)
      let that = this;
      let data = {
        "act":"status",
        "id":row.id
      }
      data['status'] = 2
      let act =  ''
      if( row.status==2 ){
        data['status'] = 1
        act =  ''
      }

      that.$confirm(''+act+'?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          // console.log("data",data)
          NewsListDelete(data)
            .then(res => {
              if (res.errcode == 0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    //
    formatText(row, column, cellValue, index){
      let content = this.decodeUnicode(cellValue);
      // console.log("content:",content)
      if(content){
        content = JSON.parse(content)
      }
      let text = content[0].text?content[0].text:content[1].text;
      // console.log("text:",text)
      text = text.slice(0,20)+'...';
      return text;
    },
    decodeUnicode(str) {  
      str = str.replace(/\\/g, "%");  
      return unescape(str);  
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 100%;
}
.el-upload-list--picture .el-upload-list__item{
  width:300px;
}
</style>

 



